import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import {
  useFeedbackRemoveStudentFeedback,
  useFeedbackUpdateStudentFeedback,
} from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { useTranslation } from "react-i18next";
import StudentFeedbackCreate from "./StudentFeedbackCreate";

const StudentFeedbackCardMenu = ({ onClose, feedback, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [feedbackRemoveStudentFeedback] = useFeedbackRemoveStudentFeedback();
  const [feedbackUpdateStudentFeedback] = useFeedbackUpdateStudentFeedback();

  const onDelete = () => {
    if (feedback?._id) {
      setDisabled((pre) => !pre);
      feedbackRemoveStudentFeedback({
        ifid: feedback?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onToggle = () => {
    setEdit((pre) => !pre);
  };
  const onEditFeedback = (data) => {
    if (feedback?._id) {
      onToggle();
      setDisabled((pre) => !pre);
      feedbackUpdateStudentFeedback({
        ifid: feedback?._id,
        studentFeedback: data,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {edit ? (
        <StudentFeedbackCreate
          onClose={onToggle}
          onEditAction={onEditFeedback}
          isAuthority
          feedback={feedback}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onToggle}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("remove")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default StudentFeedbackCardMenu;
